import React, { Component } from 'react';
import { Row, Form, Col } from 'antd';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common/index';
import { BtnSearch } from 'components/common/button';
import { getSemesters } from 'services/semesterService';
import { getFeeObjects } from 'services/feeObjectService';
import { getYearEducations } from 'services/yearEducationService';

@Form.create({ name: 'SearchBarStudentAdded' })
class SearchBarStudentAdded extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    const { yearEduId, semesterId, fOId } = getQueryParams();
    form.setFieldsValue({ yearEduId, semesterId, fOId });
  };

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lp A',
        },
        {
          id: '2',
          label: 'Lp B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getSemestersCustom = async params => {
    const res = await getSemesters(params);
    return this.updateIdToString(res);
  };

  getFeeObjectsCustom = async params => {
    const res = await getFeeObjects(params);
    return this.updateIdToString(res);
  };

  getYearEducationsCustom = async params => {
    const res = await getYearEducations(params);
    return this.updateIdToString(res);
  };

  updateIdToString = res => {
    const newItems = res.data.items.map(value => {
      const newValue = value;
      newValue.id = value.id.toString();
      return newValue;
    });
    res.data.items = newItems;
    return res;
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams({ ...this.props.paramSearch, ...values });
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Nm hc">
              {getFieldDecorator('yearEduId', {})(
                <SelectAjax
                  service={this.getYearEducationsCustom}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Hc k">
              {getFieldDecorator('semesterId', {})(
                <SelectAjax service={this.getSemestersCustom} params={{ yearEduId }} />
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item {...formItemLayout} label="i tng hc ph">
              {getFieldDecorator('fOId', {})(<SelectAjax service={this.getFeeObjectsCustom} />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <BtnSearch htmlType="submit" title="Tm kim" />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBarStudentAdded;



