let data = {
  currentPage: 1,
  isEmpty: false,
  isNotEmpty: true,
  items: [
    {
      index: 1,
      masinhvien: '14DH01109',
      hovaten: 'Bi Th Giang',
      ngaysinh: '10/10/1996',
      gioitinh: 'N',
      tenlop: 'H K Ton',
    },
    {
      index: 2,
      masinhvien: '14DH01109',
      hovaten: 'Nguyn Th Hng Giang',
      ngaysinh: '10/03/1996',
      gioitinh: 'N',
      tenlop: 'H K Ton',
    },
  ],
  resultsPerPage: 10,
  totalPages: 1,
  totalResults: 2,
};

let dataAdded = {
  currentPage: 1,
  isEmpty: false,
  isNotEmpty: true,
  items: [
    {
      index: 3,
      masinhvien: '14DH01109',
      hovaten: 'Bi Th Giang',
      ngaysinh: '10/10/1996',
      gioitinh: 'N',
      tenlop: 'H K Ton',
      doituonghocphi: 'test thu',
    },
    {
      index: 4,
      masinhvien: '14DH01109',
      hovaten: 'Nguyn Th Hng Giang',
      ngaysinh: '10/03/1996',
      gioitinh: 'N',
      tenlop: 'H K Ton',
      doituonghocphi: 'test thu',
    },
  ],
  resultsPerPage: 10,
  totalPages: 1,
  totalResults: 2,
};

export const getFeeObjectStudent = paramSearch => {
  console.log(paramSearch);
  const res = {
    data,
  };
  return res;
};

export const getFeeObjectStudentAdded = ({ yearEduId, semesterId, fOId }) => {
  console.log(yearEduId);
  console.log(semesterId);
  console.log(fOId);
  if (!yearEduId || !semesterId || !fOId)
    return {
      data: {},
    };
  const res = {
    data: dataAdded,
  };
  return res;
};

export const addStudentAdded = value => {
  const students = value.studentSelected || {};

  let newItems = data.items || [];
  const newAddedItems = dataAdded.items || [];

  newItems = newItems.filter(newItem => !students[newItem.index]);
  data = { ...data, items: newItems };

  Object.values(students).forEach(student => {
    student.doituonghocphi = 'test thu';
    newAddedItems.push(student);
  });
  dataAdded = { ...dataAdded, items: newAddedItems };
};

export const removeStudentAdded = value => {
  const newItems = data.items || [];
  let newAddedItems = dataAdded.items || [];

  newAddedItems = newAddedItems.filter(newAddedItem => !value[newAddedItem.index]);
  dataAdded = { ...dataAdded, items: newAddedItems };

  Object.values(value).forEach(student => {
    student.doituonghocphi = 'test thu';
    newItems.push(student);
  });
  data = { ...data, items: newItems };
};



