import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    // form.setFieldsValue(getQueryParams());
    const { lop, trainTypeId, facultyId, courseId, studyFieldId } = getQueryParams();
    form.setFieldsValue({ lop, trainTypeId, facultyId, courseId, studyFieldId });
  };

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lp A',
        },
        {
          id: '2',
          label: 'Lp B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams({ ...this.props.paramSearch, ...values });
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <FilterPathEducate form={this.props.form} classes />
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lp">
              {getFieldDecorator('lop', {})(
                <SelectAjax service={this.getLop} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <BtnSearch htmlType="submit" title="Tm kim" />
            <BtnClear onClick={this.handleReset} />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



