import React, { Component } from 'react';
import { TableData } from 'components/common/index';

export default class TableStudentData extends Component {
  state = {};

  columns = [
    {
      title: 'M sinh vin',
      dataIndex: 'masinhvien',
      key: 'masinhvien',
    },
    {
      title: 'H v tn',
      dataIndex: 'hovaten',
      key: 'hovaten',
    },
    {
      title: 'Ngy sinh',
      dataIndex: 'ngaysinh',
      key: 'ngaysinh',
    },
    {
      title: 'Gii tnh',
      dataIndex: 'gioitinh',
      key: 'gioitinh',
    },
    {
      title: 'Tn lp',
      dataIndex: 'tenlop',
      key: 'tenlop',
    },
  ];

  render() {
    const { service, rowSelection, paramSearch, reload } = this.props;
    return (
      <TableData
        reload={reload}
        rowSelection={rowSelection}
        paramSearch={paramSearch}
        columns={this.columns}
        service={service}
        rowKey="index"
      />
    );
  }
}



