import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Btn, BtnAddNew, BtnDelete, BtnExport } from 'components/common/button/index';
import {
  getFeeObjectStudent,
  getFeeObjectStudentAdded,
  addStudentAdded,
  removeStudentAdded,
} from 'services/feeObjectStudent';
import { getQueryParams } from 'utils/urlUtil';
import { Card, Row, Icon } from 'antd';
import TableStudentData from './TableStudentData';
import TableStudentDataAdded from './TableStudentDataAdded';
import SearchBar from './SearchBar';
import SearchBarStudentAdded from './SearchBarStudentAdded';
import './style.scss';

class FeeObjectStudent extends Component {
  state = {
    paramSearch: {},
    studentSelected: {},
    studentSelectedRowKey: [],
    studentAddedSelected: {},
    studentAddedSelectedRowKey: [],
    students: [],
    reloadTable: 0,
    reloadTableAdded: 0,
    hasLoadedAdded: false,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getFeeObjectStudentAdded = paramSearch => {
    const res = getFeeObjectStudentAdded(paramSearch);
    if (res.data && res.data.items) {
      this.setState({
        hasLoadedAdded: true,
      });
    } else {
      this.setState({
        hasLoadedAdded: false,
      });
    }
    return res;
  };

  handleChangeQueryParams = () => {
    this.setState({
      paramSearch: getQueryParams(),
    });
  };

  updateStudentKey = (student, subjectIndex) => {
    student.key = `${student.masinhvien} - ${subjectIndex}`;
    return student;
  };

  addStudent = () => {
    const { reloadTable, reloadTableAdded, studentSelected, paramSearch } = this.state;
    const { yearEduId, semesterId, fOId } = paramSearch;
    addStudentAdded({ studentSelected, yearEduId, semesterId, fOId });
    this.setState({
      studentSelectedRowKey: [],
      studentAddedSelectedRowKey: [],
      reloadTable: reloadTable + 1,
      reloadTableAdded: reloadTableAdded + 1,
      studentSelected: {},
    });
  };

  removeStudent = () => {
    const { reloadTable, reloadTableAdded, studentAddedSelected } = this.state;
    removeStudentAdded(studentAddedSelected);
    this.setState({
      studentSelectedRowKey: [],
      studentAddedSelectedRowKey: [],
      reloadTable: reloadTable + 1,
      reloadTableAdded: reloadTableAdded + 1,
      studentAddedSelected: {},
    });
  };

  enableAddBtn = () =>
    this.state.studentSelected &&
    Object.values(this.state.studentSelected).length > 0 &&
    this.state.hasLoadedAdded;
  enableDeleteBtn = () =>
    this.state.studentAddedSelected && Object.values(this.state.studentAddedSelected).length > 0;
  enablePrintAndExport = () => this.state.students.length > 0;

  render() {
    const {
      reloadTable,
      reloadTableAdded,
      paramSearch,
      studentSelectedRowKey,
      studentAddedSelectedRowKey,
    } = this.state;
    const studentSelection = {
      selectedRowKeys: studentSelectedRowKey,
      onChange: (selectedRowKeys, selectedRows) => {
        const studentSelected = {};

        selectedRows.forEach(selectedRow => {
          studentSelected[selectedRow.index] = selectedRow;
        });

        this.setState({
          studentSelected,
          studentSelectedRowKey: selectedRowKeys,
        });
      },
    };

    const studentAddedSelection = {
      selectedRowKeys: studentAddedSelectedRowKey,
      onChange: (selectedRowKeys, selectedRows) => {
        const studentAddedSelected = {};

        selectedRows.forEach(selectedRow => {
          studentAddedSelected[selectedRow.index] = selectedRow;
        });
        this.setState({
          studentAddedSelected,
          studentAddedSelectedRowKey: selectedRowKeys,
        });
      },
    };

    return (
      <Card>
        <Row>
          <SearchBar paramSearch={paramSearch} />
        </Row>
        <Row>
          <TableStudentData
            reload={reloadTable}
            service={getFeeObjectStudent}
            rowSelection={studentSelection}
            paramSearch={paramSearch}
          />
        </Row>
        <Row>
          <SearchBarStudentAdded paramSearch={paramSearch} />
        </Row>
        <Row id="btns-action">
          <BtnAddNew
            title="Thm SV khon thu"
            onClick={this.addStudent}
            disabled={!this.enableAddBtn()}
          />
          <BtnDelete
            title="Xa SV khon thu"
            onClick={this.removeStudent}
            disabled={!this.enableDeleteBtn()}
          />
          <Btn disabled={!this.enablePrintAndExport()}>
            <Icon type="printer" />
            In
          </Btn>
          <BtnExport disabled={!this.enablePrintAndExport()} />
        </Row>
        <Row>
          <TableStudentDataAdded
            reload={reloadTableAdded}
            service={this.getFeeObjectStudentAdded}
            rowSelection={studentAddedSelection}
            paramSearch={paramSearch}
          />
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FeeObjectStudent {...props} />
  </Page>
);



